import React from 'react';
import { StyleSheet, View, Dimensions, Platform, Text } from 'react-native';
import { unstable_createElement } from 'react-native-web';

class CreateVideoClass extends React.Component{
  constructor(props) {
    super(props);

    // console.log("Constructor props.src %s", props.src)
    this.player = React.createRef()
    // console.log("Constructore player")
    console.log(this.player)

    this.state = {
      src: props.src,
      autoPlay: true,
      muted: true,
      width: props.width,
      height: props.height,
      controls: true,
      controlsList: 'nodownload',
      disablePictureInPicture: true,
      ref: this.player
    }
    this.initSeek = props.initSeek
    this.initPlayBackRate = props.initPlayBackRate
    // console.log("Constructor")
    // console.log(this.state)
  }
  componentDidMount() {
    // console.log("Mounted")
    // console.log(this.player)
    // console.log(this.player.current)
    // console.log("PlayBack Rate %s ", this.player.current.playbackRate)
    // console.log("Default PlayBack Rate %s ", this.player.current.defaultPlaybackRate)
    this.player.current.playbackRate = this.initPlayBackRate
    // console.log("PlayBack Rate %s ", this.player.current.playbackRate)
    this.player.current.currentTime = this.initSeek
    // console.log("CurrentTime %s", this.player.current.currentTime)
  }

  render() {
    // console.log("Render") 
    // console.log(this.state)
    return unstable_createElement("video", this.state);
  }
}


const VideoPlayer = ({url, initSeek}) => {
    var currentTime = initSeek / 1000
    const window = Dimensions.get("window");

    // console.log("Video Player initSeek %d currentTime %d", initSeek, currentTime)
    // console.log("url %s", url)
    // console.log("Video src")
    // console.log(videoSrc)
    // console.log("Play Speed %f", playSpeed)
    // console.log("Device width %d height %d", window.width, window.height)
    // console.log(window)
    const videoHeight = Math.min(Math.floor(window.width / 1.7777), window.height)
    const videoWidth = Math.min(window.width, Math.floor(videoHeight * 1.7777))
    // console.log("Video height %d width %d Window height %d width %d", videoHeight, videoWidth,  window.height, window.width) 

    return (
        <View>
        <CreateVideoClass 
          src={url}
          initSeek={currentTime}
          width={videoWidth}
          height={videoHeight}
          initPlayBackRate="0.5"
        ></CreateVideoClass>
        </View>
    );
};

const styles = StyleSheet.create({
    backgroundVideo: {
    },
    menuContainer: {
      flex: 1,
      alignItems: 'left',
      justifyContent: 'center',
      paddingTop: 10,
      paddingBottom: 200,
      backgroundColor: '#ecf0f1',
    },    
});

export default VideoPlayer;